@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Kanit", sans-serif;
}

.gridHeader {
  display: grid;
  grid-template-rows: 1.1fr 1fr 1.7fr;
  /* grid-template-areas: "leftBody rightBody"; */
  /* column-gap: 3vw; */
  /* padding: 1vw; */
}

.searchBar {
  width: 100%;
  height: 5vh;
  background-color: white;
  display: flex;
  flex-direction: row;
  padding: 10px;
}

.header {
  width: 100%;
  height: 5vh;
  background-color: rgb(34, 34, 143);
  color: white;
  padding: 5px;
  display: flex;
  justify-content: left;
  align-items: center;
}

.hardCodedText1 {
  margin-left: 5vw;
}
.hardCodedText2 {
  margin-left: 1.5vw;
}

.logoImg {
  margin-top: 0.3vw;
  margin-left: 1vw;
  width: 2vw;
  height: 4vh;
}

form.example input[type="text"] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 100%;
  background: #f1f1f1;
}

form.example button {
  margin-top: 0px;
  float: right;
  width: 20%;
  padding: 10px;
  background: #2196f3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "leftBody rightBody";
  grid-column-gap: 4vw;
  -webkit-column-gap: 4vw;
          column-gap: 4vw;
  padding: 1vw;
}

.leftBody {
  grid-area: leftBody;
  border: 1px solid rgb(200, 200, 200);
  width: 100%;
  height: auto;
  padding: 1vw;
}
.textHeadLeft {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 10px;
}

.textHeadLeft2 {
  font-size: 2vmin;
  font-weight: 1000;
  padding: 7px;
  margin-top: 20px;
}
.rightBody {
  grid-area: rightBody;
  border: none;
  width: 100%;
  height: auto;
}

.ordinaryText {
  font-size: 1.7vmin;
  color: rgb(78, 78, 78);
}

input[type="radio"] {
  transform: scale(0.75);
}

@media (max-width: 800px) {
  .grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "rightBody";
    padding: 1vw;
  }

  .leftBody {
    display: none;
  }
}

/* .rowMerchant {
  grid-auto-columns: 1fr 3fr;
} */

.merchantBox {
  width: auto;
  height: 30vh;
  /*background-color: rgb(181, 211, 141);*/
  border: 1px solid rgb(210, 230, 240);
  margin-bottom: 5px;
  padding: 4px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.titleContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.openCloseSticker {
  margin-top: 1vh;
}

.isOpenSticker {
  margin-left: 10px;
  width: 3vw;
  height: 2vh;
  border-radius: 2px;
  color: white;
  background-color: rgb(43, 190, 6);
  font-size: 9px;
  font-weight: 700;
  padding: 1px;
  text-align: center;
}

.isCloseSticker {
  margin-left: 10px;
  width: 3vw;
  height: 2vh;
  border-radius: 2px;
  color: white;
  background-color: rgb(182, 179, 179);
  font-size: 9px;
  font-weight: 700;
  padding: 1px;
  text-align: center;
}

.secondTitleContainer {
  font-size: 1.8vh;
  margin-top: 0.5vh;
  color: rgb(140, 140, 140);
}

.keedkeed {
  margin-top: 2vh;
  margin-bottom: 1vh;
  font-size: 0.8vh;
  color: rgb(200, 200, 200);
}
.merchantImg {
  float: left;
  width: 16.5vw;
  height: 100%;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.centerClosedContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.merchantImgClosed {
  float: left;
  width: 16.5vw;
  height: 100%;
  opacity: 60%;
}

.NAimg {
  position: absolute;
  width: 5vw;
  height: 10vh;
  z-index: 10;
}

.merchantTextbox {
  margin-left: 20px;
  /* background-color: rgb(239, 239, 241);*/
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.iconImg {
  margin-top: 0.3vw;
  margin-left: 0.3vw;
  width: 2vw;
  height: 4vh;
}

.shopNameText {
  margin-top: 5px;
  font-size: 2.5vmin;
  font-weight: 1000;
}

.additionalInfo {
  font-size: 1.8vh;
  margin-top: 2vh;
  color: rgb(140, 140, 140);
}

.boldmenu {
  color: rgb(94, 94, 94);
}

.facility {
  margin-top: 1.7vh;
}

